<template>
  <div class="tutorial-container">
    <div class="card-container">
      <CardsTutorial
        :tutorialStep="pageNumber"
        @fix-tutorial-finished="fixTutorialFinished"
        @read-tutorial-finished="readTutorialFinished"
        @save-tutorial-finished="saveTutorialFinished"
        @swap-tutorial-finished="swapTutorialFinished"
      ></CardsTutorial>
    </div>

    <div class="slides-container">
      <div class="slide" v-if="pageNumber === 1">
        <div class="slide-left-container"></div>
        <div class="slide-text-container">
          <div class="main-text">
            <p>
              Die <strong>Werte</strong> aller Karten sind während der
              Bearbeitung der Aufgabe grundsätzlich <strong>verdeckt</strong>.
            </p>
          </div>
          <div class="additional-text">
            Diese Ausgangssituation ist kennzeichnend für ein Computerprogramm,
            für das die Werte zu Beginn nicht bekannt sind und das Hilfsmittel
            benötigt, um diese verarbeiten zu können.
          </div>
        </div>
      </div>

      <div class="slide" v-else-if="pageNumber === 2">
        <div class="slide-left-container"></div>
        <div class="slide-text-container">
          <div class="main-text">
            <p>
              Für das Sortieren der Karten stehen Ihnen deshalb verschiedene
              <strong>Hilfsmittel</strong> zur Verfügung.
            </p>
          </div>
          <div class="additional-text"></div>
        </div>
      </div>
      <div class="slide" v-else-if="pageNumber === 3">
        <div class="slide-left-container"><EyeIcon /></div>
        <div class="slide-text-container">
          <div class="main-text">
            <p>
              Das Hilfsmittel <strong>Lesen</strong> verwenden Sie, indem Sie
              auf das (Auge-)Symbol unterhalb einer Karte klicken. Sie richten
              damit den Lesezeiger auf die Karte und können deren Wert lesen.
              Probieren Sie es aus, indem Sie auf das Auge unter verschiedenen
              Karten klicken.
            </p>
          </div>
          <div class="additional-text">
            <p>
              Die meisten Menschen würden wahrscheinlich zuerst alle Karten
              umdrehen, um sich einen Überblick zu verschaffen. Ein
              Computerprogramm kann das jedoch nicht. Deswegen wird in dieser
              Aufgabe ein Lesezeiger eingesetzt, der die Vorgehensweise eines
              Computerprogrammes beim Lesen einer Karte simuliert.
            </p>
          </div>
          <div class="error-text" v-html="errorMessage"></div>
        </div>
      </div>

      <div class="slide" v-else-if="pageNumber === 4">
        <div class="slide-left-container"></div>
        <div class="slide-text-container">
          <div class="main-text">
            Bedenken Sie, dass die Werte verdeckter Karten für ein
            Computerprogramm unbekannt sind, auch wenn sie schon einmal gelesen
            wurden.
          </div>
          <div class="additional-text">
            Das "Vergessen" der Zahlenwerte bereits gesehener Karten ist für uns
            Menschen schwer nachvollziehbar, da wir uns solche Werte spontan
            merken und nicht vergessen können.
          </div>
        </div>
      </div>

      <div class="slide" v-else-if="pageNumber === 5">
        <div class="slide-left-container">
          <h3 class="slide-left-container-header">MERKEN</h3>
          <div class="slide-left-container-content">
            <div class="btn"><PinIcon :color="'green'" /></div>
          </div>
        </div>
        <div class="slide-text-container">
          <div class="main-text">
            Mit einem Klick auf das Hilfsmittel <strong>Merken</strong> können
            Sie den Wert einer sichtbaren Karte sichtbar halten, auch wenn der
            Lesezeiger nicht mehr auf diese Karte gerichtet ist. Wird der
            Merkzeiger auf eine andere Karte gesetzt, steht der Wert nicht mehr
            zur Verfügung. Setzen Sie nun den Merkzeiger auf die Karte, deren
            Wert Sie sehen und stellen Sie anschließend den Lesezeiger auf eine
            andere Karte. Wie Sie sehen, können Sie durch die Kombination der
            Hilfsmittel <strong>Lesen</strong> und <strong>Merken</strong> die Werte zweier Karten sichtbar
            machen.
          </div>
          <div class="additional-text">
            Wir Menschen merken uns viele Dinge - ob wir wollen oder nicht. Ein
            Computerprogramm „merkt“ sich nur Dinge, die ihm explizit vorgeben
            werden. Daher sind die Werte verdeckter Karten für ein
            Computerprogramm immer unbekannt. Für den Vergleich von Zahlenwerten
            werden jedoch zwei „sichtbare“ Karten benötigt. Mit Hilfe des
            Merkzeigers ist es jetzt möglich, zwei Kartenwerte gleichzeitig
            sehen zu können.
          </div>
          <div class="error-text" v-html="errorMessage"></div>
        </div>
      </div>

      <div class="slide" v-else-if="pageNumber === 6">
        <div class="slide-left-container"><SwapIcon /></div>
        <div class="slide-text-container">
          <div class="main-text">

            Um die Reihenfolge der Karten zu ändern, tauschen Sie jeweils zwei Karten durch Klicken und Ziehen mit der Maus (Drag &#38; Drop). Versuchen Sie es! Klicken Sie auf eine der beiden Karten mit sichtbarem Wert, halten Sie die Maustaste gedrückt und ziehen Sie die Karte auf jene, mit der Sie tauschen möchten. Den Tausch schließen Sie ab, indem Sie auf das grüne Häkchen klicken. Der Klick auf „x“ bricht den Tausch ab.

          </div>
          <div class="additional-text"></div>
          <div class="error-text" v-html="errorMessage"></div>
        </div>
      </div>

      <div class="slide" v-else-if="pageNumber === 7">
        <div class="slide-left-container"></div>
        <div class="slide-text-container">
          <div class="main-text">
            <p>Aber beachten Sie:</p>
            <p></p>
            <p>
Werden zwei Karten in ihrer Position verändert (= Sortierschritt), muss zumindest eine der beiden Karten nach Abschluss der Tauschoperation an ihrem endgültigen Platz zu liegen kommen.
            </p>
          </div>
          <div class="additional-text">
          </div>
        </div>
      </div>
      <div class="slide" v-else-if="pageNumber === 8">
        <div class="slide-left-container"><FixIcon /></div>
        <div class="slide-text-container">
          <div class="main-text">
            <strong>Und zuletzt:</strong> Mit dem <strong>Hilfsmittel „Fixieren“</strong> grenzen
            Sie den Bereich bereits sortierter Zahlen vom Bereich noch zu
            sortierender Zahlen ab. Klicken Sie dazu auf das Wort „FIX“ auf dem
            jeweiligen Platz. Mit jeder sortierten Karte muss der Fixierbereich
            erweitert werden. Im fixierten Bereich sind dann auch keine
            Operationen mehr möglich. Versuchen Sie, die Aufgabe möglichst
            effizient, d. h. mit wenigen Schritten zu lösen.
          </div>
          <div class="additional-text">
Damit das Programm nicht „vergisst“, welche Zahlen schon sortiert sind und wo noch zu sortieren ist, verwendet es die Fixierfunktion. 

          </div>
          <!-- <div class="error-text" v-html="errorMessage"></div> -->
        </div>
      </div>
      <!--<div class="slide" v-else-if="pageNumber === 8">
        <div class="slide-left-container"></div>
        <div class="slide-text-container">
          <div class="main-text">
            Die Sortierung ist abgeschlossen, wenn die zu sortierenden Karten
            auf ihren richtigen Plätzen liegen.
          </div>
          <p><br /></p>
          <div>
            Und hier noch einmal alle Vorgaben und Regeln in Kürze:
            <ul>
              <li>
                Die Karten sollen nach der Größe ihres Wertes sortiert werden -
                links der kleinste, rechts der größte Wert.
              </li>
              <li>
                Der Lesezeiger macht den Wert einer verdeckten Karte sichtbar.
              </li>
              <li>
                Die Werte verdeckter Karten sind unbekannt, auch wenn sie schon
                einmal gelesen wurden.
              </li>
              <li>Der Merkzeiger hält eine Karte sichtbar.</li>
              <li>Die Karten werden durch Drag &#38; Drop getauscht.</li>
              <li>
                Mindestens eine Karte muss nach einem Tausch auf ihrem
                endgültigen Platz liegen.
              </li>
              <li>Die sortierten Karten werden zum Abschluss fixiert.</li>
              <li>
                Die Sortierung soll in möglichst wenigen Schritten erfolgen.
              </li>
            </ul>
            Klicken Sie auf Start, um mit der Aufgabe zu beginnen.
          </div>
          <div class="additional-text"></div>
        </div>
      </div>-->
    </div>

    <div class="navigation-container">
      <div class="navigation">
        <div class="prev" @click="previousPage">&#10094;</div>
        <span class="page-number">{{ pageNumber }}</span>
        <span v-for="i in pages" :key="i"
          ><div class="dot" :class="{ 'active-dot': pageNumber >= i }"></div
        ></span>
        <span class="page-number">8</span>
        <div
          class="next"
          :class="{ hidenext: pageNumber == 8 }"
          @click="nextPage"
        >
          &#10095;
        </div>
      </div>
      <div @click="skipTutorial" class="skip-tutorial" v-if="pageNumber != 8">
        Anleitung überspringen
      </div>
      <div @click="skipTutorial" class="skip-tutorial" v-if="pageNumber == 8">
        Start
      </div>
    </div>
  </div>
</template>

<script>
import CardsTutorial from "@/components/inf/sorting/Assignment1/Tutorial/CardsTutorial.vue";
import EyeIcon from "@/assets/inf/sorting/icons/EyeIcon.vue";
import PinIcon from "@/assets/inf/sorting/icons/PinIcon.vue";
import SwapIcon from "@/assets/inf/sorting/icons/SwapIcon.vue";
import FixIcon from "@/assets/inf/sorting/icons/FixIcon.vue";

export default {
  components: {
    EyeIcon,
    PinIcon,
    SwapIcon,
    FixIcon,
    CardsTutorial,
  },
  data() {
    return {
      pageNumber: 1,
      pages: 8,
      readFinished: false,
      saveFinished: false,
      fixFinished: false,
      swapFinished: false,
      errorMessage: "",
    };
  },
  emits: ["tutorial-finished"],
  mounted() {
    setTimeout(() => {
      window.scrollTo(0, 0);
    });
  },
  methods: {
    nextPage() {
      //check if read tutorial is finished
      if (this.pageNumber == 3 && !this.readFinished) {
        //set error message
        this.errorMessage =
          "Klicken Sie nacheinander auf das Auge unter den beiden Karten. Sie können fortsetzen, wenn Sie beide Karten gelesen haben!";
        return;
      }
      //check if save tutorial is finished
      if (this.pageNumber == 5 && !this.saveFinished) {
        //show error message
        this.errorMessage =
          "Um zur nächsten Seite zu gelangen, machen Sie zwei Karten sichtbar und klicken Sie dann auf den blauen Pfeil unten <em>rechts</em>.";
        return;
      }
      if (this.pageNumber == 7 && !this.swapFinished) {
        //show error message
        this.errorMessage =
          "Um zur nächsten Seite zu gelangen, nehmen Sie zumindest einen Tausch vor und klicken Sie dann auf den blauen Pfeil unten <em>rechts</em>.";
        return;
      }
      if (this.pageNumber == 9) {
        this.$emit("tutorial-finished");
        return;
      }
      this.pageNumber++;
      this.pageNumber = Math.min(this.pages, this.pageNumber);
      this.errorMessage = "";
    },
    previousPage() {
      this.pageNumber--;
      this.pageNumber = Math.max(1, this.pageNumber);
    },
    readTutorialFinished() {
      this.readFinished = true;
    },
    saveTutorialFinished() {
      this.saveFinished = true;
    },
    fixTutorialFinished() {
      this.fixFinished = true;
    },
    swapTutorialFinished() {
      this.swapFinished = true;
    },
    skipTutorial() {
      this.$emit("tutorial-finished");
    },
  },
};
</script>

<style scoped>
.slide {
  display: flex;
  flex-flow: row nowrap;
  min-height: 300px;
}

.slide-text-container {
  border: 1px solid black;
  border-radius: 10px;
  min-width: 80%;
  max-width: 80%;
  min-height: 400px;
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0;
  padding: 20px;
}
.slide-left-container {
  border: 1px solid black;
  border-radius: 10px;
  width: 20%;
  display: flex;
  flex-direction: column;
}

.slide-left-container-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.additional-text {
  padding-top: 10px;
  font-style: italic;
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0;
}

.error-text {
  padding-top: 10px;
  font-weight: bold;
  color: red;
}

/* ----- NAVIGATION ------ */
.navigation-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
}

.navigation {
  flex: 0 1 auto;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}

.prev,
.next {
  cursor: pointer;
  width: auto;
  padding: 25px;
  font-weight: bolder;
  font-size: 40px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
  color: #006bac;
}

.hidenext {
  visibility: hidden;
}

/* The dots/bullets/indicators */
.dot {
  height: 5px;
  width: 10px;
  margin: 0 2px;
  background-color: #006bac;
  opacity: 0.3;
  transition: background-color 0.6s ease;
}

.active-dot {
  background-color: #006bac;
  opacity: 1;
}

.skip-tutorial {
  cursor: pointer;
  flex: 0 1 auto;
  margin-left: auto;
}

.skip-tutorial:hover {
  text-decoration: underline;
}

.instruction {
  font-weight: bold;
}

.btn {
  background-color: white;
  border: 1px solid black;
  border-radius: 10px;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin: 10px;
}
</style>
