        von Informationen ohne viel überlegen aufnehmen und verarbeiten können.
        Ein Computer kann Informationen aber nur nacheinander abarbeiten und
        benötigt genaue Instruktionen dafür. Diese Instruktionen werden als eine
        Folge von Anweisungen gegeben, einen sogenannten Algorithmus. Dieser
        legt fest, was der Computer in welcher Reihenfolge machen soll, in
        diesem Fall wie er sortieren soll.
      </p>
      <p>
