        selbstverständlich, dass die Zahl „1“ vor der Zahl „2“ in einer
        aufsteigend sortierten Liste stehen muss. Beispielsweise muss mittels
        einer Anweisung beschrieben werden, dass, wenn die Zahl einer Karte
