        werden müssen.
      </p>
      <p>
        Um einen Sortieralgorithmus zu erstellen, müssen mehrere Dinge beachtet
        werden. Sie haben vielleicht bereits bemerkt, dass der Aufwand für das
        Sortieren von 10 Karten wesentlich geringer ist als für 1000 Karten.
        Dieser Umstand wird als Problemgröße bezeichnet. Diese hat einen
        wesentlichen Einfluss auf die Effizienz eines Algorithmus und somit auch
        auf seine Laufzeit. Die Laufzeit eines Algorithmus gibt an, wie viele
        Schritte benötigt werden, um zu einem Ziel zu kommen. Im Falle von
        Sortieralgorithmen bedeutet das, wie viele Operationen (z.B. Vergleichs-
        oder Verschiebeoperationen) sind notwendig, um eine Menge von Elementen
        in eine geordnete Reihenfolge zu bringen. Die Laufzeit kann dabei je
        nach Komplexität und Umfang der Problemgröße und nach dem verwendeten
        Algorithmus unterschiedlich sein.
      </p>
      <p>
        In der Regel wird diese mittels der Landau-Notation oder auch O-Notation
        angegeben. Dabei werden meistens das Best-Case, Average-Case und
        Worst-Case Scenario angegeben. Wie der Name bereits verrät, geben diese
        an, wie viele Schritte der Algorithmus im besten, durchschnittlichen und
        im schlechtesten Fall benötigt, um eine Menge von Elementen zu
        sortieren.
      </p>
      <p>
        Computer sortieren mit Hilfe von Sortieralgorithmen. Sortieralgorithmen
        werden in mehrere Kategorien eingeteilt. Hier werden Sie zwei wichtige
        Unterscheidungsmerkmale kennenlernen:
      </p>
      <ol>
        <li>stabile vs. instabile Verfahren</li>
        <li>in-place vs. out-of-place Verfahren</li>
      </ol>
    </div>
    <div class="row">
      <h2>Was sind stabile vs. instabile Sortierverfahren?</h2>
      <p>
        Ein Sortieralgorithmus wird als stabil bezeichnet, wenn er die relative
        Reihenfolge von Elementen nicht verändert. Gegeben sein beispielsweise
        eine Liste von Personen, die bereits nach den Namen sortiert ist. Nun
        wird neu nach dem Geburtsjahr sortiert. Bei einem stabilen Verfahren
        bleiben Personen mit gleichem Geburtsjahr weiterhin nach Namen sortiert,
        das heißt, es kann immer nur genau ein Endergebnis geben.
      </p>
      <p>
        Bei instabilen Verfahren ist das nicht der Fall und es kann, nachdem
        nach Geburtsjahr sortiert wurde, nicht garantiert werden, dass Personen
        mit gleichem Geburtsjahr weiterhin nach Namen sortiert bleiben. Es
        können also verschiedene Endergebnisse nach Sortierung nach Geburtsjahr
        entstehen.
      </p>
    </div>
    <div class="row">
      <h2>Was sind in-place vs. out-of-place Sortierverfahren?</h2>
      <p>
        Als in-place werden Verfahren bezeichnet, welche nur einen konstanten
        zusätzlichen Speicherbedarf zum Sortieren benötigen. Beispielweise nur
        eine Speicherstelle für eine Tauschoperation von zwei Elementen. Dabei
        ist wichtig zu beachten, dass in-place Verfahren die Eingabedaten mit
        den Ausgabedaten überschreibt. In der folgenden Abbildung können Sie
        eine Darstellung für eine solche Tauschoperation mittels einer
        zusätzlichen Speicherstelle sehen.
      </p>
      <p>
        Ein Algorithmus wird hingegen als out-of-place bezeichnet, wenn er die
        Ausgabedaten in eine neue Liste speichert und somit die Eingabedaten
        nicht überschreibt. Überlegen Sie kurz, was das für eine große Menge
        bzw. eine kleine Menge von Elementen bedeutet. In der Regel sind
        In-Place Verfahren langsamer als out-of-place Verfahren, benötigen aber
        nur wenig zusätzlichen Speicher.
      </p>
    </div>
    <div class="row">
