        Reihenfolge zu bringen. Beispiele hierfür sind Selection Sort, Bubble
        Sort, Quick Sort oder Insertion Sort. Es ist wichtig zu verstehen, dass
        jedes dieser Sortierverfahren in unterschiedlichen Situationen Vor- oder
        Nachteile hat.
      </p>
    </div>-->
