<template>
  <div class="container">
    <AssignmentbarSorting1
      :showHelpBtn="false"
      :showDismissBtn="true"
      :showFinishBtn="false"
    ></AssignmentbarSorting1>
    <div v-if="showDescription">
      <h1>Sortieren mit System!</h1>
      <p>
        Für uns Menschen sind Aufgaben, wie zum Beispiel das aufsteigende
        Sortieren von fünf Karten mit unterschiedlichen einstelligen natürlichen
        Zahlen, keine große Sache. Wir übersehen die 5 Karten mit einen Blick,
        finden jene mit der kleinsten Zahl sofort heraus, dann die nächste usw.
        - fertig! Wir brauchen keinen Plan oder Algorithmus, um ein Problem
        dieser Größenordnung zu lösen. Weil wir uns vieles „im Vorübergehen“
        merken und leicht unsere Lösungsstrategie anpassen können, sind wir sehr
        flexibel und schnell - aber nur bei der Lösung kleiner Probleme.
      </p>
      <p>
        Ein <em>Computerprogramm</em> benötigt schon vor Beginn der Bearbeitung
        einer solchen Aufgabe einen Plan, wie das Problem gelöst werden soll.
        Das mag bei einer geringen Zahl von Sortierobjekten unökonomisch
        erscheinen. Ist dieser Plan aber einmal festgelegt, braucht ein Computer
        im Unterschied zum Menschen für 1 Million Karten nicht merkbar länger
        als für fünf.
      </p>
      <p>
        Bei der Lösung der folgenden Aufgabe sollen Sie nach Regeln vorgehen,
        die jenen ähneln, nach denen auch Computerprogramme arbeiten. Die damit
        verbundene Denkweise scheint uns Menschen bisweilen umständlich und
        fremd, aber sie entspricht den besonderen Voraussetzungen der
        „Denkmaschine“ Computer. Hat man diese einmal verstanden, lassen sich
        damit viele Probleme in einer überlegenen Weise lösen, zu der wir als
        Menschen ohne Computer nicht in der Lage wären.
      </p>
      <h2>Aber nun zur Aufgabe:</h2>
      <p>
        Vor Ihnen befindet sich eine Reihe von Karten, wobei nur deren (leere)
        Rückseiten zu sehen sind. Die Vorderseiten sind mit einer Zahl versehen,
        die sich in der Reihe in dieser Aufgabe nicht wiederholt. Die Karten
        sind zufällig angeordnet, und jede Karte nimmt genau einen Platz ein.
        Ihre Aufgabe besteht nun darin, alle Karten nach der Größe ihres
        Zahlenwertes zu sortieren, sodass jene mit dem geringsten Wert ganz
        links und jene mit dem größten ganz rechts zu liegen kommt.
      </p>
      <p>
        Doch gilt es einige Regeln zu beachten. Mit diesen möchten wir Sie im
        nächsten Schritt bekannt machen.
      </p>
      <p>
        <em
          >Für die Bearbeitung dieser Aufgabe sollten Sie ca. 15 Minuten und
          evtl. einen Zeitpuffer vorsehen. Die Bearbeitung darf nicht
          unterbrochen werden, wenn Sie eine Rückmeldung zur Lösung erhalten
          wollen. Bitte nehmen Sie diese Aufgabe nur in Angriff, wenn Sie über
          ausreichend Zeit verfügen und die Aufgabe in Ruhe bis zum Ende
          bearbeiten können.
        </em>
      </p>
      <div class="custom-btn" @click="startTutorial">Start</div>
      <div class="custom-btn-secondary" @click="backBtnClicked">Zurück</div>
    </div>

    <div id="sorting-tutorial-container">
      <SlidesTutorial
        v-if="showTutorial"
        @tutorial-finished="finishIntro"
        @skip-tutorial="skipIntro"
      ></SlidesTutorial>
    </div>
  </div>
</template>

<script>
import SlidesTutorial from "@/components/inf/sorting/Assignment1/Tutorial/SlidesTutorial.vue";
import AssignmentbarSorting1 from "@/components/inf/sorting/Assignment1/AssignmentbarSorting1.vue";

export default {
  components: {
    SlidesTutorial,
    AssignmentbarSorting1,
  },
  emits: ["assignment1-tutorial-back","assignment1-tutorial-finished"],
  data() {
    return {
      showDescription: true,
      showTutorial: false,
      showEndTutorial: false,
    };
  },
  mounted() {
    setTimeout(() => {
      window.scrollTo(0, 0);
    });
  },
  methods: {
    startTutorial() {
      this.showDescription = false;
      this.showTutorial = true;
    },
    backBtnClicked() {
      this.$emit("assignment1-tutorial-back");
    },
    finishIntro() {
      this.showDescription = false;
      this.showTutorial = false;
      this.$emit("assignment1-tutorial-finished");
    },
    endAssignment() {
      this.showDescription = true;
      this.showTutorial = false;
      this.showEndTutorial = false;
    },
  },
};
</script>

<style scoped>
.custom-btn,
.custom-btn-secondary {
  float: right;
}
</style>
